import "@/assets/style.css";
import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import { createPrismic } from "@prismicio/vue";
import DesktopHeader from "@/components/ui/DesktopHeader.vue";
import MobileHeader from "@/components/ui/MobileHeader.vue";
import "lazysizes";
import VuePlyr from "vue-plyr";
import "vue-plyr/dist/vue-plyr.css";
import DrawButton from "@/components/ui/DrawButton.vue";
import SkeletonBox from "@/components/ui/SkeletonBox.vue";
import ShopBasket from "@/components/shop/ShopBasket.vue";
import "@/assets/plyr-style.css";
import "@/assets/snipcart-style.css";
import VueSnip from "vue-snip";
import SwiperCore, { Navigation } from "swiper";

// install Swiper modules
SwiperCore.use([Navigation]);

const prismic = createPrismic({
  endpoint: "https://roxanakenjeeva.cdn.prismic.io/api/v2",
});

const app = createApp(App);
app.use(router);
app.use(prismic);
app.use(VueSnip);
app.use(VuePlyr, {
  plyr: {
    controls: [
      "play-large", // The large play button in the center
      "play", // Play/pause playback
      "progress", // The progress bar and scrubber for playback and buffering
      "current-time", // The current time of playback

      "pip", // Picture-in-picture (currently Safari only)
      "airplay", // Airplay (currently Safari only)

      "fullscreen", // Toggle fullscreen
    ],
  },
});
app.component("DesktopHeader", DesktopHeader);
app.component("MobileHeader", MobileHeader);
app.component("DrawButton", DrawButton);
app.component("SkeletonBox", SkeletonBox);
app.component("ShopBasket", ShopBasket);

app.mount("#app");
