<template>
  <div>
    <div v-if="showHeader">
    <DesktopHeader v-if="isDesktop" v-cloak />
    <MobileHeader v-if="!isDesktop" v-cloak />
    </div>
    <transition name="shopview" mode="out-in">
      <ShopBasket v-show="onShop" />
    </transition>

    <router-view v-slot="slotProps">
      <transition name="route" mode="out-in">
        <component :is="slotProps.Component"></component>
      </transition>
    </router-view>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isDesktop: true,
      onShop: false,
      showHeader: false,
    };
  },
  methods: {
    subIsActive() {
      const path = this.$route.path;
      if (path.includes("/shop")) {
        this.onShop = true;
      } else {
        this.onShop = false;
      }
    },
    getScreenSize() {
      let screenSize = window.innerWidth;
      if (screenSize > 768) {
        this.isDesktop = true;
      } else {
        this.isDesktop = false;
      }
      this.showHeader = true;
    },
  },
  beforeCreate() {
    this.$nextTick(() => {
      window.addEventListener("load", () => {
        this.getScreenSize();
      });
      window.addEventListener("resize", () => {
        this.getScreenSize();
      });
      
    });
  },
  watch: {
    $route() {
      this.subIsActive();
    },
  },
};
</script>



