<template>
 
    <HomePageSeven  :key="pageResized" />

</template>

<script>
import HomePageSeven from "@/components/home/HomePageSeven.vue";

export default {
  name: "Home",
  components: {
    HomePageSeven,
  },
  data() {
    return {
      pageResized: false,
    }
  },
    beforeCreate() {
    this.$nextTick(() => {
      window.addEventListener("resize", () => {
       this.pageResized = !this.pageResized
      });
         });
  }

};
</script>

